
.logo {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.logo-icon {
  display: inline-block;
  width: 100%;
  height: 100%;
  margin-bottom: 6px;
}

@keyframes slideline {
 from {
   width: 0px;
 }

 to {
   width: 100%;
 }
}

.logo-icon:hover ~ .logo-bottom {
  animation: slideline .5s ease-in-out;
}


.logo-bottom {
  width: 100%;
  height: 0px;
  border: 1px solid;
}
