.home {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 76px 0 260px;
}

.home-main {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 138px;
}


.info {
  display: flex;
  flex-direction: column;
}

.info-title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 224px;
  padding: 10px;
  background-color: #262627;
  margin-bottom: 12px;
}

.info-desc {
  width: 600px;
  color: #262627;
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
}

.info-desc-normal {
  height: 56px;
  font-weight: 600;
  font-size: 40px;
  line-height: 56px;
  margin-bottom: 12px;
}

.info-desc-big {
  height: 81px;
  font-weight: 600;
  font-size: 78px;
  line-height: 81px;
  margin-bottom: 12px;
}

.button-group {
  display: flex;
  align-items: center;
  height: 42px;
  margin-bottom: 30px;
}

.button-group > button:first-child {
  margin-right: 14px;
}

.extra {
  display: flex;
  align-items: center;
}

.extra-icon {
  width: 13.5px;
  height: 13.5px;
  margin-right: 8.5px;
}

.extra-title {
  margin-right: 8px;
  font-size: 14px;
  height: 16px;
  line-height: 16px;
  color: #333;
}

.extra-link-icon {
  width: 18px;
  height: 18px;
}

.banner-logo {
  width: 541px;
  height: 541px;
  background: url('@/assets/banner-logo-circle.svg') no-repeat;
  background-size: contain;;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-logo > img {
  width: 396px;
  height: 400px;
}

.bottom-line {
  margin-top: 57px;
  display: block;
  width: 1038px;
  height: 10px;
  background: url('@/assets/bottom-arrow-line.svg') no-repeat;
  background-size: cover;
}


@media screen and (max-width: 1440px) {
  .home {
    padding: 0;
  }

  .home-main {
    flex-direction: column-reverse;
    margin-top: 3px;
  }

  .info {
    align-items: center;
  }

  .info-title {
    font-size: 32px;
    height: 60px;
    line-height: 60px;
    margin-bottom: 8px;
  }

  .info-desc {
    height: 22px;
    line-height: 22px;
    font-size: 16px;
    width: 220px;
    height: 66px;
    margin-bottom: 24px;
  }

  .banner-logo {
    width: 323px;
    height: 323px;
  }

  .banner-logo > img  {
    width: 267.3px;
    height: 270px;
  }

  .button-group {
    margin-bottom: 24px;
  }

  .bottom-line {
    display: none;
  }
}
