.qa {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.qa-header {
  height: 60px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  margin-bottom: 20px;
  padding-right: 86px;
}

.qa-header-logo {
  height: 22px;
  width: 108px;
}

.qa-main {
  width: 100%;
  display: flex;
  padding-left: 427px;
}

.qa-main-side {
  display: flex;
  margin-right: 20px;
  height: 32px;
  align-items: center;
}

.qa-step-number {
  height: 17px;
  font-size: 24px;
  line-height: 17px;
  font-weight: 600;
  color: #262627;
  margin-right: 4px;
}

.qa-side-icon {
  width: 18px;
  height: 18px;
}


.qa-main-body {
  width: 100%;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
}

.qa-main-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  height: 32px;
  color: #262627;
  margin-bottom: 20px;
}


.qa-table> div {
  margin-bottom: 8px;
}

.qa-table {
  margin-bottom: 24px;
}

.qa-button-group {
  width: 100%;
  display: flex;
}
.qa-button-group > button:first-child {
  margin-right: 12px;
}

.qa-button-group-sm {
  display: none;
}


@media screen and (max-width: 1440px) {

  .qa-header {
    height: 36px;
  }

  .qa-header-logo {
    display: none;
  }

  .qa-main {
    padding-left: 32px;
  }

  .qa-main-side {
    margin-right: 12px;
  }

  .qa-main-title {
    font-size: 18px;
  }

  .qa-table {
      margin-bottom: 40px;
  }

  .qa-button-group {
    display: none;
  }

  .qa-button-group-sm {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
  }

  .qa-button-group-sm > button:first-child {
    margin-bottom: 12px;
  }
}
