.login {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 123px;
}

.logo-text {
  width: 108px;
  height: 22px;
  margin-bottom: 29px;
}

.login-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #262627;
  height: 32px;
  margin-bottom: 3px;
}

.login-desc {
  font-weight: 500;
  font-size: 14px;
  line-height: 32px;
  color: #262627;
  height: 32px;
  margin-bottom: 24px;
}

.input-group {
  width: 330px;
  margin-bottom: 24px;
}

.input-group > div {
  margin-bottom: 12px;
}


@media screen and (max-width: 1440px) {
  .login {
    padding-top: 41px;
  }

  .login-desc {
    margin-bottom: 34px;
  }

  .input-group {
    width: 327px;
  }

  .input-group > div > input {
    height: 32px;
  }

  .login > button{
    width: 327px;
  }
}
