.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.progress-bar {
  height: 4px;
  background: #FFAC46;
  flex-shrink: 0;
  transition: width .5s ease-in-out;
}

.main {
  flex: 1;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: 1440px;
  min-width: 320px;
}


.main > .container {
  display: flex;
  flex: 1;
}


@media screen and (max-width: 1440px) {
  .main {
    max-width: 375px;
  }
}
