.join {
  width: 100%;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
}

.join-header {
  display: flex;
  align-items: center;
  padding-left: 200px;
  height: 22px;
}

.header-icon {
  width: 18px;
  height: 18px;
  margin-right: 9px;
}

.header-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  height: 22px;
  color: #fff;
}

.join-main {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.join-main::after {
  content: '';
  width: 454px;
  height: 402px;
  background: url(@/assets/join-test-bg.svg) no-repeat;
  background-size: contain;
  left: 50%;
  top: 165px;
  transform: translate3d(-50%, 0, 0);
  position: absolute;
}

.join-logo {
  width: 108px;
  height: 22px;
  margin-top: 176px;
  margin-bottom: 32px;
  z-index: 1;
}

.join-title {
  color: #fff;
  font-weight: 500;
  font-size: 36px;
  line-height: 56px;
  height: 56px;
  margin-bottom: 30px;
  z-index: 1;
}

.button-group {
  display: flex;
  align-items: center;
}

.button-group > button:first-child {
  border: none;
  margin-right: 12px;
}

.button-group > button:nth-child(2) {
  background: none;
  border: 1px solid #fff;
  opacity: 0.5;
}


@media screen and (max-width: 1440px) {
  .join {
    padding-top: 164px;
  }

  .join-header {
    order: 1;
    margin-top: 230px;
    padding-left: 0px;
    justify-content: center;
  }

  .join-main::after {
    max-width: 454px;
    width: 100%;
  }

  .join-logo {
    margin-top: 0px;
    margin-bottom: 80px;
  }

  .join-title {
    font-size: 26px;
    line-height: 36px;
    width: 299.5px;
    height: 72px;
    text-align: center;
    margin-bottom: 24px;
  }
}

